import {useEffect, useState} from "react";
import axios from "axios";
import {
    Avatar, Button, Dialog,
    Grid, IconButton,
    Link,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
    Typography
} from "@mui/material";

import {CoineusCryptoFormat} from "../utils/currency_format";
import {useSelector} from "react-redux";
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong';

import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';

export default function Transactions(props) {

    const {wallet} = useSelector(state => state.coineus);


    const [transactions, setTransactions] = useState([]);

    const [open, setOpen] = useState(false);

    useEffect(() => {
        if (!open) return;
        axios.post(
            "https://api.thegraph.com/subgraphs/name/somemoecoding/surgeswap-v1-eth",
            {
                query: `
                {
                  boughts(
                    first: 100
                    where:{
                      from :"${wallet.address}"
                    }
                    orderBy: blockTimestamp,
                    orderDirection:desc
                  ) {
                    from
                    to
                    transactionHash
                    blockTimestamp
                    tokens
                    beans
                    dollarBuy
                  }
                  solds(
                    first: 100
                    where:{
                      to :"${wallet.address}"
                    }
                    orderBy: blockTimestamp,
                    orderDirection:desc
                  ) {
                    from
                    to
                    transactionHash
                    blockTimestamp
                    tokens
                    beans
                    dollarSell
                  }
                }
            `
            }
        ).then(resp => {
            const {boughts, solds} = resp.data.data;

            boughts.map(b => {
                b.type = 'buy'
            })

            solds.map(b => {
                b.type = 'sell'
            })

            const tmp = [...boughts, ...solds];

            tmp.sort((a, b) => {
                if (a.blockTimestamp > b.blockTimestamp) {
                    return -1;
                }

                if (a.blockTimestamp < b.blockTimestamp) {
                    return 1;
                }

                return 0;
            })

            setTransactions(tmp)
        })
    }, [open])

    return <>
        <IconButton
            color="primary"
            size="small"
            onClick={() => setOpen(true)}
            style={{marginRight: -10, display: wallet.address !== undefined ? '' : 'none'}}
        ><ReceiptLongIcon/></IconButton>
        <Dialog
            open={open}
            fullScreen
            PaperProps={{style: {backgroundColor: '#000', backgroundImage: 'none', padding: 20}}}
        >
            <Grid container spacing={2}>
                <Grid item xs={12} textAlign="right">
                    <Button onClick={() => setOpen(false)}>Close</Button>
                </Grid>
                <Grid item xs={12}>

                    <List>
                        {
                            transactions.length === 0 &&
                            <Typography style={{padding: 20}}>No transactions</Typography>
                        }
                        {
                            transactions.map(t => {
                                return <Paper style={{marginBottom: 10}}>
                                    <ListItem key={t.transactionHash}>
                                        <ListItemAvatar>
                                            <Avatar style={{backgroundColor:"#000"}}>
                                                {
                                                    t.type === 'buy' ?
                                                        <ArrowUpwardIcon style={{
                                                            color: '#00FF00'
                                                        }}/> :
                                                        <ArrowDownwardIcon style={{
                                                            color: 'red'
                                                        }}/>
                                                }
                                            </Avatar>
                                        </ListItemAvatar>

                                        <ListItemText
                                            primary={(new Date(t.blockTimestamp * 1000)).toLocaleString()}
                                            primaryTypographyProps={{fontWeight: 700, fontSize:12}}
                                            secondary={<Link href={`https://etherscan.io/tx/${t.transactionHash}`}
                                                             target="_blank" style={{textDecoration: 'none'}}>View
                                                Tx</Link>}
                                            secondaryTypographyProps={{fontSize:11}}
                                        />
                                        <ListItemText
                                            primary={<>
                                                {`${CoineusCryptoFormat(t.tokens / 10 ** 9)} SRG`}
                                            </>}
                                            secondary={`${CoineusCryptoFormat(t.beans / 10 ** 18)} ETH`}
                                            primaryTypographyProps={{textAlign: 'right', fontWeight: 700, fontSize:12}}
                                            secondaryTypographyProps={{textAlign: 'right', fontSize:11}}
                                        />
                                    </ListItem>
                                </Paper>
                            })
                        }

                    </List>

                </Grid>
            </Grid>
        </Dialog>
    </>
}